<template>
  <el-form ref="dataForm" :model="temp" :rules="passwordRules">
    <el-form-item label="帐号">
      <el-input v-model.trim="userName" :disabled="true" />
    </el-form-item>
    <el-form-item label="旧密码" prop="Password">
      <el-input
        v-model.trim="temp.Password"
        type="password"
        tabindex="1"
        show-password
      />
    </el-form-item>
    <el-form-item label="新密码" prop="NewPassword">
      <el-input
        v-model.trim="temp.NewPassword"
        type="password"
        tabindex="2"
        show-password
      />
    </el-form-item>
    <el-form-item label="确认密码" prop="ConfirmPassword">
      <el-input
        v-model.trim="temp.ConfirmPassword"
        type="password"
        tabindex="3"
        show-password
      />
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submit">更新</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import { changePassword } from "@/api/user";
import { getUserId } from "@/utils/auth";
export default {
  props: {
    userName: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      temp: { Id: "", Password: "", NewPassword: "", ConfirmPassword: "" },
      passwordRules: {
        Password: [
          {
            required: true,
            trigger: "blur",
            validator: (rule, value, callback) => {
              if (value.length < 6) {
                callback(new Error("密码不能少于6位"));
              } else {
                callback();
              }
            },
          },
        ],
        NewPassword: [
          {
            required: true,
            trigger: "blur",
            validator: (rule, value, callback) => {
              if (value.length < 6) {
                callback(new Error("密码不能少于6位"));
              } else if (value === this.temp.Password) {
                callback(new Error("新密码不能与旧密码相同"));
              } else {
                callback();
              }
            },
          },
        ],
        ConfirmPassword: [
          {
            required: true,
            trigger: "blur",
            validator: (rule, value, callback) => {
              if (value.length < 6) {
                callback(new Error("密码不能少于6位"));
              } else if (value != this.temp.NewPassword) {
                callback(new Error("确认密码与新密码不一致"));
              } else {
                callback();
              }
            },
          },
        ],
      },
    };
  },
  created() {
    this.temp.Id = getUserId();
  },
  methods: {
    submit() {
      this.$refs.dataForm.validate((valid) => {
        if (valid) {
          let { ConfirmPassword, ...temp } = this.temp;
          changePassword(temp).then((response) => {
            this.$message({
              message: "密码修改成功",
              type: "success",
              duration: 3 * 1000,
            });
          });
        } else {
          return false;
        }
      });
    },
  },
};
</script>
