<template>
  <el-card class="user-card" style="margin-bottom: 20px">
    <div slot="header" class="header">
      <span>关于我</span>
      <svg-icon v-if="!editable" icon-class="edit" @click="handleEdit" />
    </div>

    <div class="user-profile">
      <div class="box-center">
        <pan-thumb
          :image="userInfo.avatar"
          :height="'100px'"
          :width="'100px'"
          :hoverable="false"
        >
        </pan-thumb>
      </div>
      <div class="box-center">
        <div class="user-name text-center">你好,{{ userInfo.RealName }}</div>
        <div class="user-role text-center text-muted">
          {{ userInfo.role | uppercaseFirst }}
        </div>
      </div>
    </div>

    <div class="user-bio">
      <div class="user-education user-bio-section">
        <div class="user-bio-section-header">
          <svg-icon icon-class="dashboard" /><span>个人信息</span>
        </div>
        <div class="user-bio-section-body">
          <el-form ref="dataForm" :rules="rules" :model="userInfo">
            <el-form-item label="性别" prop="Gender">
              <el-select
                v-if="editable"
                v-model="userInfo.Gender"
                placeholder="请选择"
                clearable
                style="width: 100%"
                class="filter-item"
              >
                <el-option
                  v-for="(item, index) in genderOptions"
                  :key="item"
                  :label="item"
                  :value="index"
                />
              </el-select>
              <span v-else>{{ genderOptions[userInfo.Gender] }}</span>
            </el-form-item>
            <el-form-item label="生日" prop="Birthday">
              <el-date-picker
                v-if="editable"
                v-model="userInfo.Birthday"
                type="date"
                placeholder="选择日期"
                style="width: 100%"
                format="yyyy-MM-dd"
                value-format="yyyy-MM-dd"
              >
              </el-date-picker>
              <span v-else>{{ userInfo.Birthday }}</span>
            </el-form-item>
            <el-form-item label="手机号" prop="Mobile">
              <el-input
                v-if="editable"
                v-model.trim="userInfo.Mobile"
                :disabled="true"
                maxlength="11"
                show-word-limit
              />
              <span v-else>{{ userInfo.Mobile }}</span>
            </el-form-item>
            <el-form-item label="邮箱" prop="Email">
              <el-input v-if="editable" v-model.trim="userInfo.Email" />
              <span v-else>{{ userInfo.Email }}</span>
            </el-form-item>
            <el-form-item v-if="editable">
              <el-button type="primary" @click="submit">更新</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </el-card>
</template>

<script>
import PanThumb from "@/components/PanThumb";

export default {
  components: { PanThumb },
  props: {
    user: {
      type: Object,
      default: () => {
        return {
          RealName: "",
          Gender: "",
          Birthday: "",
          Mobile: "",
          Email: "",
          Portrait: "",
          avatar: "",
          role: "",
        };
      },
    },
  },
  data() {
    return {
      editable: false,
      genderOptions: ["未知", "女", "男"],
      userInfo: {
        RealName: "",
        Gender: "",
        Birthday: "",
        Mobile: "",
        Email: "",
        Portrait: "",
        avatar: "",
        role: "",
      },
      rules: {
        Mobile: [
          {
            required: false,
            message: "手机号格式不正确",
            trigger: "blur",
            validator: (rule, value, callback) => {
              if (value && !validPhoneNumber(value)) {
                callback(new Error("请输入正确的手机号"));
              } else {
                callback();
              }
            },
          },
        ],
        Email: [
          {
            required: false,
            trigger: "blur",
            validator: (rule, value, callback) => {
              if (value && !validEmail(value)) {
                callback(new Error("邮箱格式不正确。"));
              } else {
                callback();
              }
            },
          },
        ],
      },
    };
  },
  created() {
    this.userInfo = { ...this.user };
  },
  methods: {
    handleEdit() {
      this.editable = !this.editable;
    },
    submit() {
      this.editable = !this.editable;
      let user = this.user;
      let userInfo = this.userInfo;
      if (
        user.Gender == userInfo.Gender &&
        user.Birthday == userInfo.Birthday &&
        user.Mobile == userInfo.Mobile &&
        user.Email == userInfo.Email
      ) {
        return;
      }
      let { role, avatar, ...data } = this.userInfo;
      this.$store
        .dispatch("user/changeUser", data)
        .then(() => {
          this.$message({
            message: "用户信息更新成功",
            type: "success",
            duration: 3 * 1000,
          });
        })
        .catch(() => {});
    },
  },
};
</script>

