<template>
  <el-form>
    <el-form-item label="帐号">
      <el-input v-model.trim="user.UserName" :readonly="true" />
      <!-- <span>{{ user.name }}</span> -->
    </el-form-item>
    <el-form-item label="手机号">
      <el-input v-model.trim="user.UserName" :readonly="true" />
      <!-- <span>{{ user.name }}</span> -->
    </el-form-item>
    <el-form-item label="性别">
      <el-input v-model.trim="user.email" />
    </el-form-item>
    <el-form-item label="生日">
      <el-input v-model.trim="user.email" />
    </el-form-item>
    <el-form-item label="邮箱">
      <el-input v-model.trim="user.email" />
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submit">更新</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  props: {
    user: {
      type: Object,
      default: () => {
        return {
          name: "",
          email: "",
        };
      },
    },
  },
  methods: {
    submit() {
      this.$store
        .dispatch("user/changeUser", { Email: this.user.email })
        .then(() => {
          this.$message({
            message: "用户信息更新成功",
            type: "success",
            duration: 3 * 1000,
          });
        })
        .catch(() => {});
    },
  },
};
</script>
