<template>
  <div class="app-container">
    <div v-if="user">
      <el-row :gutter="20">
        <el-col :span="6" :xs="24">
          <user-card :user="user" />
        </el-col>

        <el-col :span="18" :xs="24">
          <el-card>
            <el-tabs v-model="activeTab">
              <el-tab-pane v-if="false" label="Activity" name="activity">
                <activity />
              </el-tab-pane>
              <el-tab-pane v-if="false" label="Timeline" name="timeline">
                <timeline />
              </el-tab-pane>
              <el-tab-pane v-if="false" label="帐号" name="account">
                <account :user="user" />
              </el-tab-pane>
              <el-tab-pane label="密码修改" name="password">
                <password :user-name="UserName" />
              </el-tab-pane>
            </el-tabs>
          </el-card>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import UserCard from "./components/UserCard";
import Activity from "./components/Activity";
import Timeline from "./components/Timeline";
import Account from "./components/Account";
import Password from "./components/Password";

export default {
  name: "Profile",
  components: { UserCard, Activity, Timeline, Account, Password },
  data() {
    return {
      user: {},
      activeTab: "password",
    };
  },
  computed: {
    ...mapGetters([
      "UserName",
      "RealName",
      "Gender",
      "Birthday",
      "Mobile",
      "Email",
      "Portrait",
      "avatar",
      "roles",
    ]),
  },
  created() {
    this.getUser();
  },
  methods: {
    getUser() {
      this.user = {
        UserName: this.UserName,
        RealName: this.RealName,
        Gender: this.Gender,
        Birthday: this.Birthday,
        Mobile: this.Mobile,
        Email: this.Email,
        Portrait: this.Portrait,
        role: this.roles.join(" | "),
        avatar: this.avatar,
      };
    },
  },
};
</script>
